// pages / dashboard / facility / manage-booking

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class ManageBooking extends React.Component {
    componentDidMount() {
        $('.manage-booking-table').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]

        });
    }

    rejectBooking() {
        $('#modalRejectBooking').modal('show');
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
                <span>

                    {/* Start of Helmet */}
                    <Helmet title="Facility - manage booking" meta={[
                        {
                            name: 'description',
                            content: 'Facility - manage booking'
                        }, {
                            name: 'keywords',
                            content: 'Facility'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-8">
                            <h2>Manage Booking</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Manage Booking</strong>
                                </li>
                            </ol>
                        </div>
                    </div>
                    {/* End of Heading */}


                    {/* Start of Modal */}
                    <div className="modal inmodal" id="modalRejectBooking" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated bounce">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-exclamation-triangle modal-icon"></i>
                                    <h4 className="modal-title">Are you sure?</h4>
                                    This action is irreversible!
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="firstName">Rejected Reason *</label>
                                            <div className="col-lg-10">
                                                <textarea className="form-control textarea-no-resize" rows="4" placeholder="Rejected Reason" ref="message" maxLength="255"></textarea>
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-danger">Proceed</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Modal */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInDown">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>All Booking</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                    <i className="fa fa-times"></i>
                                                </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover manage-booking-table">
                                                <thead>
                                                    <tr>
                                                        <th>Booking ID</th>
                                                        <th>Booking Date &amp; Time</th>
                                                        <th>Booking Duration</th>
                                                        <th>Facility Involved</th>
                                                        <th>Booked By</th>
                                                        <th>Date of this Booking</th>
                                                        <th>Status</th>
                                                        <th>Booking Actions</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            <small>0id9d2-21e2d122-21d2-d212d1d</small>
                                                        </td>
                                                        <td>
                                                            <small>13.09.2017 - 5:00 pm to 8:00 pm </small>
                                                        </td>
                                                        <td>
                                                            3 Hours
                                                        </td>
                                                        <td>
                                                            Gym Room
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">Larry Romaell</Link>
                                                        </td>
                                                        <td>
                                                            <small>13.07.2017 - 5:60 pm</small>
                                                        </td>
                                                        <td>
                                                            <span className="label label-info">Awaiting ...</span>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.rejectBooking.bind(this)}>Reject</a> | <a href="#">Approve</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            <small>0id9d2-21e2d122-21d2-d212d1d</small>
                                                        </td>
                                                        <td>
                                                            <small>13.09.2017 - 5:00 pm to 8:00 pm </small>
                                                        </td>
                                                        <td>
                                                            3 Hours
                                                        </td>
                                                        <td>
                                                            Multipurpose Hall
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">Franky Losaprel</Link>
                                                        </td>
                                                        <td>
                                                            <small>13.07.2017 - 5:60 pm</small>
                                                        </td>
                                                        <td>
                                                            <span className="label label-primary">Approved</span>
                                                        </td>
                                                        <td>
                                                            <a href="#">edit</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            <small>0id9d2-21e2d122-21d2-d212d1d</small>
                                                        </td>
                                                        <td>
                                                            <small>13.09.2017 - 5:00 pm to 8:00 pm </small>
                                                        </td>
                                                        <td>
                                                            3 Hours
                                                        </td>
                                                        <td>
                                                            Swimming Pool
                                                        </td>
                                                        <td>
                                                            <Link to="/dashboard/account/view">Trevior Drank</Link>
                                                        </td>
                                                        <td>
                                                            <small>13.07.2017 - 5:60 pm</small>
                                                        </td>
                                                        <td>
                                                            <span className="label label-danger">Rejected</span>
                                                        </td>
                                                        <td>
                                                            <a href="#">edit</a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(ManageBooking);



